import React , {useState ,useEffect } from 'react'
import './style.css'
import data from './data'
import { FaQuoteRight ,FaAngleLeft , FaAngleRight} from 'react-icons/fa';
function Reviews() {
const [index , setIndex]= useState(0) 
const {name , job , image , text } = data[index];

// change the title
useEffect(()=>{
 document.title='Reviews App'
})
//Check if number is large or small than array length
const checkNumper = (numper)=>{
 if(numper > data.length -1){
  return 0
 }
 if(numper < 0){
  return data.length -1
 }
 return numper;
}
// previus button function
const prevPerson =()=>{
 setIndex((index)=>{
  let newIndex = index -1;
  return checkNumper(newIndex)
 })
}
// next button function
const nextPerson =()=>{
 setIndex((index)=>{
  let newIndex = index +1;
  return checkNumper(newIndex)
 })
}
// random number function for surbrise me button
const randomNumber = ()=>{
 let randomNumber = Math.floor(Math.random()* data.length)
 if(randomNumber === index){
  randomNumber = index+1;
 }
 setIndex(checkNumper(randomNumber));
}

// html content
 return (
  <section>
   <div className="container">
    <div className="header">
     <h1>Our Reviews</h1>
     <div className="theline"></div>
    </div>
    <article>
    <div>
      <div className="reviews">
      <div className="imgContainer">
       <img src={image} alt="##"/>
       <div className='quots'>
        <FaQuoteRight  />
       </div>
      </div>
      <div className="author">
       <h2>{name}</h2>
       <h3>{job}</h3>
      </div>
      <div className="info">
       <p>{text}</p>
      </div>
      <div className="buttonsContainer">
       <button onClick={()=> prevPerson()}><FaAngleLeft /></button>
       <button  onClick={()=> nextPerson()}><FaAngleRight /></button>
      </div>
      <div className="surpriseMe">
       <button onClick={()=> randomNumber()}>Surprise Me</button>
      </div>
    </div>
    </div>
    </article>
   </div>
  </section>
 )
}

export default Reviews
